@import "../../../../mixin.scss";
@import "../../../../App.scss";

.headerSP {
  padding: 10px;
  height: 56px;
  &__menu-btn {
    width: 34px;
    height: 34px;
    @include flex_box();
    @include flex_content_center();
    @include flex_align_center();
    cursor: pointer;
    border: solid 1px #000000;
    font-size: 27px;
    color: #ffffff;
    background: $corporation-color;
  }
  &__menu-content {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: -100%;
    padding: 0 !important;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.3s;
    z-index: 100;
    &.opened {
      left: 0;
      visibility: visible;
      pointer-events: auto;
      transition: all 0.3s;
    }

    &__header {
      @include flex_box();
      @include flex_align_center();
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 36px;
      font-size: 16px;
      background-color: $corporation-color;
      color: #ffffff;
      z-index: 150;
      &__greeting {
        padding-left: 10px;
      }
      &__close {
        width: 28px;
        height: 28px;
        margin-left: auto;
        font-size: 28px;
        cursor: pointer;
      }
    }

    &__body {
      position: absolute;
      width: 100%;
      overflow-y: auto;
      z-index: 140;
      -ms-overflow-style: none; /* scroll 非表示 IE, Edge 対応 */
      scrollbar-width: none; /* scroll 非表示 Firefox 対応 */
      &::-webkit-scrollbar {
        display: none; /* scroll 非表示 chrome, safari */
      }
      &__main-menu {
        width: 100%;
        height: 282px;
        display: grid;
        background-color: $bg-color1;
        padding: 10px;
        &__btn {
          @include flex_box();
          @include flex_content_center();
          @include flex_align_center();
          padding: 0 10px;
          &.customer-list {
            grid-row: 1/2;
            grid-column: 1/3;
          }
          &.customer-regist {
            grid-row: 2/3;
            grid-column: 1/2;
          }
          &.customer-search {
            grid-row: 2/3;
            grid-column: 2/3;
          }
          &.file {
            grid-row: 3/4;
            grid-column: 1/2;
          }
          &.support-regist {
            grid-row: 3/4;
            grid-column: 2/3;
          }
          .button {
            background-color: $sub-color1;
            border: solid 1px $border-color1;
            color: $font-color3;
            height: 72px;
            width: 100%;
          }
        }
      } // headerSP__menu-content__body__main-menu

      &__menu-list {
        .menu {
          &__category {
            height: 36px;
            line-height: 36px;
            padding-left: 10px;
            background-color: $corporation-color;
            color: $font-color2;
          }
          &__item {
            height: 34px;
            line-height: 34px;
            padding-left: 10px;
            background-color: $bg-color2;
            border-bottom: solid 1px $border-color1;
            &.logout {
              border-top: solid 1px $border-color1;
              border-bottom: solid 1px $border-color1;
            }
          }
          &__user-info {
            position: relative;
            height: 230px;
            padding: 0 10px;
            background-color: $bg-color1;
            &__item {
              height: 30px;
              line-height: 30px;
            }
            &__remake-pass {
              position: absolute;
              top: 10px;
              right: 10px;
              width: 90px;
              height: 25px;
              font-size: 6px;
            }
          }
        }
      } // headerSP__menu-content__body__list-menu
    } // headerSP__menu-content__body
  } // headerSP__menu-content
}
